// Tournament types
export type TournamentStatus = 'registration' | 'in_progress' | 'finished';
export type ParticipantStatus = 'pending' | 'confirmed' | 'rejected' | 'active' | 'eliminated';

export type TournamentDiscipline = 
  | 'FREE_PYRAMID'
  | 'FREE_PYRAMID_EXTENDED'
  | 'COMBINED_PYRAMID'
  | 'DYNAMIC_PYRAMID'
  | 'COMBINED_PYRAMID_CHANGES';export interface TournamentParticipant {
  id: number;
  playerId: number;
  playerName: string;
  rating: number;
  status: ParticipantStatus;
  seed?: number;
  registeredAt: string;
  confirmedAt?: string;
  registeredByAdmin: boolean;
}

export interface Tournament {
  id: number;
  name: string;
  description?: string;
  status: TournamentStatus;
  registrationStart?: string;
  registrationEnd: string;
  startDate?: string;
  endDate?: string;
  startedAt?: string;
  finishedAt?: string;
  city: string;
  country: string;
  club: string;
  discipline: TournamentDiscipline;
  isRated: boolean;
  createdByAdminId: number;
  createdAt: string;
  registeredCount: number;
  isRegistered?: boolean;
  registeredPlayers?: TournamentParticipant[];
}

export interface AvailablePlayer {
  id: string;
  name: string;
  rating: number;
  matchesPlayed: number;
}


